.content {
  padding: 50px;
  width: 100%;	
  background: #fff;

  &__empty {
    width: 100%;
    color: $text-light;
    font-size: 2rem;
    font-weight: 700;
    align-self: center;
    text-align: center;
  }

  &-todo-form {
    input {
      padding: 10px;
      margin-bottom: 8px;
    }
    .btn-submit {
      margin-right: 1rem;
    }
  }

  &-folder {
    .input__folder-title {
      cursor: text;
    }
    &__title,
    &__title .input__folder-title {
      font-size: 2rem;
      font-weight: 700;
      display: inline-block;
      word-break: break-all;
      cursor: pointer;
      margin: 0;
      background-color: transparent;

      &:hover{
        svg {
          fill: #c3c3c3;
        }
      }
      svg {
        margin-left: 15px;
      }
    }

    &__todos {
      margin-bottom: 1rem;
    }

    &__add-btn {
      color: $text-light;
      font-size: 1rem;
      line-height: 1.5rem;
      padding: 3px 6px;
  
      i {
        margin-right: 10px;
        font-size: 1.5rem;
        vertical-align: bottom;
      }
    }
  }
}